.form{
    transition: all 0.5s ;
      animation: appearing 0.3s ease-out;
      @keyframes appearing {
          0% {
            opacity: 0;
          }
        
          100% {
            opacity: 1;
          }
        }
    width: 940px;
    min-height: 580px;
    margin: 150px auto 50px auto;
    background-color: white;
    border: 1px solid #d9d9d9;
    border-radius: 6px;
    box-shadow: 0px 0.6087964177131653px 2.9332919120788574px 0px #00000005;
    box-shadow: 0px 1.4630224704742432px 7.049108028411865px 0px #00000007;
    box-shadow: 0px 2.7547414302825928px 13.272845268249512px 0px #00000009;
    box-shadow: 0px 4.91398811340332px 23.67648696899414px 0px #0000000B;
    box-shadow: 0px 9.19107437133789px 44.28426742553711px 0px #0000000D;
    padding: 30px 32px;
    animation: appearing 0.3s ease-in-out;
  
    .header{
      margin-bottom: 20px;
      text-align: center;
      font-family: 'Roboto';
      font-size: 20px;
      font-weight: 500;
      line-height: 28px;
      letter-spacing: 0em;
      margin-top: 20px;
    }
  
    .inputs {
      text-decoration: none;
      width: 900px; 
      margin: 0 auto; 
  
      div {
          position: relative;
          margin-bottom: 25px; 
          label, input {
              display: block; 
              width: 100%;
          }
  
          label {
              margin-bottom: 5px; 
              color: #000; 
              font-size: 16px; 
          }
          textarea {
            width: 880px;
            margin-top: 5px;
            height: 170px ; 
            padding: 5px 10px;
            border: 1px solid #D9D9D9;
            border-radius: 4px;
            resize: none;
            font-size: 16px; 
          }
          textarea.body::placeholder{
            vertical-align: top;
            text-align: left;
            font-size: 16px; 
            font-family: 'Roboto';
            color: #D9D9D9
          }
          input {
              margin-top: 5px;
              box-sizing: border-box; 
              height: 40px; 
              border: 1px solid #D9D9D9;
              border-radius: 4px;
              padding: 0 10px;
              font-size: 16px; 
          }
          input::placeholder{
              color: #D9D9D9
          }
          .error{
              position: absolute; // Absolute position the error messages
              color: red;
              top: 100%; // Place it right below the input field
              left: 0;
              margin-top: 5px; // Provide a small space between the input and the error message
              font-size: 0.8em; 
          }
      }
    }
    .tags{
      
    }
  
    .submit{
      width: 320px;
      height: 40px;
      background-color: #1890ff;
      color: white;
      cursor: pointer;
      border: none;
      border-radius: 5px;
      font-size: 16px;
      font-family: 'Roboto' ;
      margin-top: 0px;
    }
    .tagContainer{
      display: flex;
      margin-bottom: 10px !important;
    }
    .tag{
      width: 300px !important;
    }
    .delete,
    .add {
      width: 120px;
      height: 40px;
      margin-right: 17px;
      font-size: 18px;
      display: block;
      text-align: center;
      background-color: inherit;
      border: none;
      border-radius: 5px;
      font-weight: 400;
      transition: 0.4s;
      text-decoration: none;
      cursor: pointer;
      border: 1px solid red;
      color: red;
      &:hover {
        background-color: red;
        color: white;
        border: none;
      }
    }
    .delete{
      margin: 5px 10px 0px 15px;
    }
    .add {
      margin-bottom: 24px;
      border: 1px solid #1890ff;
      color: #1890ff;
      &:hover {
        background-color: #1890ff;
        color: white;
        border: none;
      }
    }
  }